::-webkit-scrollbar {
    width: 10px;
    background-color: #001489;
}
  
::-webkit-scrollbar-thumb {
background-color: #DDBB00;
border-color: #001489;
border-style: solid;
border-width: 2px;
border-radius: 2px;
}

/* Firefox */
::-moz-scrollbar {
    width: 12px;
  }
  
  ::-moz-scrollbar-thumb {
    background-color: yellow;
  }