body {
  margin: 0px;
}

.content {
  margin: 0;
  padding-top: 30px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
  justify-items: center;
  background-color: #000745;
  min-height: 100vh;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  /* padding-bottom: 5px; */
  padding-top: 5px;
  font-size: 10pt;
  display: flex;
  flex-direction: column;
  background-color: #001489;
  color: #DDBB00;
  border-top: #DDBB00;
  border-top-style: solid;
}

.footer p{
  margin-top: 5px;
  padding-top: 0px;
}

.vote-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  border: 2px solid #001489;
  min-width: 10rem;
  padding: 1.5rem;
  width: auto;
  background-color: #FFDD00;
  color: #001489; 
  font-size: 24pt;
  cursor: pointer;
}

.vote-button.disabled {
  color: gray;
  background-color: darkgray;
  border-color: gray;
  pointer-events: none;
  cursor: default;
}

.item-pair {
  padding-top: 20px;
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.item-list {
  padding-top: 20px;
  padding-bottom: 4rem;
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.item-description-box {
  font-size: 13pt;
  font-weight: 300;
  border: 1px solid;
  border-radius: 5px;
  padding: 15px;
  background-color: #FFDD00;
  color: #001489;
  white-space: pre-line
}

.item-container {
  margin-top: 20px;
  margin-right: 20px;
  margin-left: 20px;
  width: 60%;
  max-width: 700px;
  border-style: solid;
  border-width: 3px;
  border-color: #DDBB00;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #001489;
  color: #DDBB00;
  transition: box-shadow 0.3s ease;
}

.item-container.selected {
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.5); /* Adjust the shadow as needed */
  background-color: #FFCC00;
  color: #001489;
}

.item-container img {
  width: auto;
  height: 100%;
  max-height: 50vh;
  border-style: solid;
  border-width: 2px;
  border-color: #DDBB00;
  border-radius: 6px;
}

.expand-button {
  background-color: darkgreen;
}

.navbar-left {
  background-color: #001489;
  color: white;
  width: 50%;
  height: 10vh;
  max-height: 8rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  border-bottom: #DDBB00;
  border-bottom-style: solid;
  font-weight: 700;
}

.navbar-right {
  background-color: #001489;
  color: white;
  width: 50%;
  height: 10vh;
  max-height: 8rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border-bottom: #DDBB00;
  border-bottom-style: solid;
  font-weight: 700;
}

.navbar-link-left {
  color: #DDBB00;
  font-size: 20px;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 30pt;
}

.navbar-link-left:visited {
  color: #DDBB00;
}

.navbar-link-right {
  color: #DDBB00;
  font-size: 20px;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 30pt;
}

.navbar-link-right:visited {
  color: #DDBB00;
}

.navbar-list {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin-bottom: 2rem;
}

.navbar-item {
  margin: 0 10px;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.total-votes-text {
  padding: 0.5rem;
  padding-bottom: 0;
  border-radius: 5px;
  margin-top: -1rem;
  font-size: 12pt;
  color: #DDBB00;
}

.thanks-for-voting {
  margin: 0.5rem;
  margin-top: 1rem;
  padding: 1rem;
  background-color: #FFCC00;
  font-size: 14pt;
  border-radius: 6px;
  border-style: solid;
  border-color: #001489;
  font-style: italic;
  font-weight: 500;
  color: #001489;
}

.home-page-text {
  padding-top: 1rem;
  font-size: 50pt;
  font-weight: 700;
  color: #DDBB00;
}

.home-page-explanation {
  margin-top: 2rem;
  font-style: normal;
  font-weight: 300;
  font-size: 14pt;
  padding: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  width: 70%;
  max-width: 700px;
  color: #222222;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: first baseline;
  border-style: solid;
  border-radius: 10px;
  white-space: pre-wrap;
  border-color: #DDBB00;
  background-color: #001489;
  color: #DDBB00;
}

.home-page-explanation-header {
  padding-top: 0.5rem;
  font-weight: bold;
}

.home-page-explanation-text {
  padding-bottom: 0.5rem;
  font-size: 11pt;
}

